<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="xmxx-main" style="margin-top:80px">
      <div class="container-mini">
        <div class="content">
          <common-list
            ref="ggcommonlist"
            class="common-list"
            :post-url="list.postUrl"
            :apptype-show="false"
            :req-data="list.reqData"
            :data-name="list.dataName"
            @returnData="getPrizeLog($event)"
          >
            <div v-for="(item,index) of lsjlList" :key="index">
              <div v-if="dklx===1" class="common-main box">
                <div class="page-list">
                  <div class="pagelist-left">上午打卡时间</div>
                  <div class="pagelist-right">{{ zhTime(item.ccdksj) }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">下午打卡时间</div>
                  <div class="pagelist-right">{{ zhTime(item.fcdksj) }}</div>
                </div>
              </div>

              <div v-if="dklx===2" class="common-main box">
                <div class="page-list">
                  <div class="pagelist-left">出差打卡时间</div>
                  <div class="pagelist-right">{{ zhTime(item.ccdksj) }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">出差打卡地址</div>
                  <div class="pagelist-right">{{ item.ccdkwz }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">返程打卡时间</div>
                  <div class="pagelist-right">{{ zhTime(item.fcdksj) }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">返程打卡地址</div>
                  <div class="pagelist-right">{{ item.fcdkwz }}</div>
                </div>
              </div>

              <div v-if="dklx===3" class="common-main box">
                <div class="page-list">
                  <div class="pagelist-left">拜访时间</div>
                  <div class="pagelist-right">{{ zhTime(item.ccdksj) }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">打卡位置</div>
                  <div class="pagelist-right">{{ item.ccdkwz }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">拜访客户</div>
                  <div class="pagelist-right">{{ item.bfkhmc }}</div>
                </div>
              </div>
            </div>
          </common-list>
        </div>
      </div>
    </div>
  </div>
</template>

<script land="script" scoped>
import commonList from '@/components/list/commonList.vue'
import { mapState } from 'vuex'
import headerBar from '@/components/header/headerBar'

export default {
  name: 'MoreLsjl',
  components: {
    commonList,
    headerBar
  },
  data() {
    return {
      headerData: {
        title: '历史记录',
        leftUrl: 'back',
        leftText: ' '
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      dklx: '',
      lsjlList: []
    }
  },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.dklx = this.$route.params.dklx
    this.init()
  },
  methods: {
    zhTime(time) {
      if (time) {
        time = time + ''
        return time.slice(0, 4) + '/' + time.slice(4, 6) + '/' + time.slice(6, 8) + ' ' + time.slice(8, 10) + ':' + time.slice(10, 12) + ':' + time.slice(12, 14)
      }
    },
    getPrizeLog(data) {
      this.lsjlList = data
      console.log('this.lsjlList===================================', this.lsjlList)
    },
    init() {
      this.list.postUrl = 'erp/getXsdbKqjlAction'
      this.list.reqData = {
        userId: this.userInfo.id,
        ksrq: '', // 开始日期
        jzrq: '', // 截止日期
        dkbz: this.dklx,
        pageno: 1,
        pagerow: 5
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无数据'
    }
  }
}
</script>

<style lang="css" scoped>
.xmxx-main{height:calc(100vh - 180px);}
</style>
